.header {
  background-color: #0f044c;
  height: 15vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header__title {
  color: #eeeeee;
  margin: 0;
  font-size: 3rem;
}

.footer {
  background-color: #0f044c;
  color: white;
  padding: 0.2rem;
  padding-top: 0.4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
}

.footer__text {
}

.footer__link {
}

.footer__github {
  -webkit-filter: invert(1);
          filter: invert(1);
}

.preview {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: -3rem;
  background-color: #787a91;
}

.print-container {
  width: 90vw;
  max-width: 720px;
  max-height: 1018px;
  height: calc(90vw / 210 * 297);
  margin: 20px auto;
}

@media print {
  .print-container {
    width: 100%;
    height: 100%;
    margin: 0;
  }
}

@media screen and (max-width: 500px) {
  .print-container {
    margin-bottom: calc(-10vw + 100px);
  }
}

@media screen and (min-width: 800px) {
  .preview {
    background-color: transparent;
  }
}

.CV {
  min-height: 100%;
  min-width: 100%;
  height: 100%;
  width: 100%;
  max-height: 100%;
  max-width: 100%;
  font-family: "Nunito", sans-serif;
  display: grid;
  grid-template:
    [row1-start] "header header" 15% [row1-end]
    [row3-start] "main sidebar" auto [row3-end]
    [row2-start] "footer footer" 3% [row2-end]
    / auto 30%;
  font-size: min(2.5vw, 20px);
}

.CV * {
  font-size: inherit;
}

.CV__header {
  grid-area: header;
  background-color: #112031;
  color: #d4ecdd;
  padding: 1.5vh 2vw;
  padding-left: 7%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.CV__name {
  font-size: min(3em, 65px);
  margin: 0.3vh 0;
}

.CV__title {
  font-size: min(1.7em, 35px);
  margin: 0;
  font-weight: 300;
}
.CV__main {
  grid-area: main;
  background-color: #d4ecdd;
  padding: 20px;
}

.CV__zone {
  margin-bottom: 1em;
}

.CV__zone-title {
  width: 100%;
  border-bottom: 1px solid lightslategray;
  font-weight: 400;
  font-size: 1.2em;
  margin-bottom: 0.5em;
}

.CV__entry {
  display: flex;
  justify-content: flex-start;
}

.CV__dates {
  font-weight: 600;
  height: 100%;
  min-height: 100%;
  width: 32%;
}

.CV__info {
  margin-left: 0.8em;
}

.CV__main-info {
  font-weight: 600;
}

.CV__secondary-info {
  font-weight: 300;
}

.CV__description {
  margin-left: 0.5em;
}

.CV__sidebar {
  color: #d4ecdd;
  grid-area: sidebar;
  background-color: #152d35;
  border-top: 4px solid #345b63;
  border-bottom: 4px solid #345b63;
  padding: 1.8em;
  padding-top: 4em;
  display: flex;
  flex-direction: column;
  grid-gap: 5vh;
  gap: 5vh;
}

.CV__email {
  font-size: 0.8em;
  font-weight: 500;
}

.CV__skills {
  margin-top: 50px;
}

.CV__skill-list {
  display: flex;
  justify-content: space-evenly;
  margin-top: 1em;
}

.CV__skill {
  display: inline-block;
}

.CV__footer {
  grid-area: footer;
  background-color: #112031;
}

.outer-form {
  width: 100%;
  background-color: #787a91;
  margin: 0;
  padding: 1.5rem;
  padding-bottom: 5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}

@media screen and (min-width: 800px) {
  .outer-form {
    width: 80%;
    margin: 2rem auto;
    box-shadow: 0 0 0.7rem #141e61;
    border-radius: 6px;
  }
}

@media screen and (min-width: 1080px) {
  .outer-form {
    width: 60%;
    margin: 2rem auto;
    box-shadow: 0 0 0.7rem #141e61;
    border-radius: 6px;
  }
}

.zone {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  padding: 0.5rem 1em;
}

.zone_general {
}

.zone_professional {
}

.zone_education {
}

.zone_skills {
}

.zone__entry {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 1.2rem;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}

.zone__name {
  margin: 0;
  margin-bottom: 0.5rem;
}

.button {
  --primary: #141e61;
  --secondary: #eeeeee;
  --main: var(--primary);
  --alt: var(--secondary);
  position: relative;
  padding: 0.5rem;
  width: 30vw;
  background-color: var(--main);
  color: var(--alt);
  border: 2px solid transparent;
  font-size: 1.1rem;
  border-radius: 3px;
}

.button:hover {
  border-color: var(--main);
  background-color: var(--alt);
  color: var(--main);
}

.button_remove,
.button_reset,
.button_remove-skill {
  --primary: rgba(204, 30, 30, 0.603);
}

.button_add-skill {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border-radius: 0 3px 3px 0;
}

.button_remove-skill {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 0.2rem 0.5rem;
}

.button_print {
  top: -1.45rem;
  --primary: rgb(24, 138, 71);
}

@media screen and (min-width: 800px) {
  .button_print {
    top: -3.4rem;
  }
}

.button__block {
  min-width: 100%;
  min-height: 100%;
  width: 100%;
  height: 100%;
  background-color: #787a91;
  opacity: 0.5;
  display: inline-block;
  position: absolute;
  z-index: 99;
  top: 0;
  left: 0;
}

.inner-form {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding-top: 0.5rem;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}

.inner-form__input {
  width: 100%;
  background-color: #eeeeee;
  border: 0;
  border-left: 0.3rem solid transparent;
  outline: none;
  padding: 0.4rem;
  border-radius: 3px;
}

.inner-form__input:focus {
  border-color: #0f044c;
}

.inner-form__textarea {
  padding: 10px;
  padding-right: 15px;
  padding-bottom: 15px;
  height: 120px;
  resize: vertical;
}

.inner-form__textarea-container {
  position: relative;
  width: 100%;
  order: 98;
}

.char-count {
  opacity: 1;
  color: grey;
  display: inline-block;
  position: absolute;
  z-index: 99;
  bottom: 12px;
  right: 10px;
  text-align: center;
  font-size: 15px;
}

.inner-form__name {
  order: 1;
}
.inner-form__title {
  order: 2;
}

.inner-form__address {
  order: 3;
}

.inner-form__phone {
  order: 4;
}

.inner-form__email {
  order: 5;
}

.inner-form__company {
  order: 3;
}
.inner-form__city {
  order: 4;
}

.inner-form__from {
  width: 48.5%;
  margin-right: auto;
  order: 5;
}

.inner-form__to {
  width: 48.5%;
  order: 6;
}

.inner-form__degree {
  order: 1;
}

.inner-form__school {
  order: 2;
}

.inner-form__clear {
  order: 99;
}

.form-skills {
  display: flex;
  margin-top: 0.5rem;
}

.form-skills__input {
  border-radius: 3px 0 0 3px;
}

.skills-list {
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: unset;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}

.skills-list__item {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
  font-size: 1.3rem;
}

